import React, { useState } from 'react';
import styled from 'styled-components';
import CloudArchitecture from './components/CloudArchitecture';
import ExecutiveDashboard from './components/ExecutiveDashboard';
import CustomerJourney from './components/CustomerJourney';
import TechArchComparison from './components/TechArchComparison';
import Button from './components/Button';
import Card from './components/Card';
import { H1, H2, H3, Text, Body } from './components/Typography';
import './App.css';

const AppContainer = styled.div`
  min-height: 100vh;
  background: linear-gradient(135deg, #F8FAFC 0%, #E2E8F0 100%);
  padding-bottom: 32px;
`;

const Header = styled.header`
  background: linear-gradient(135deg, #3B82F6, #1D4ED8);
  color: white;
  padding: 24px 0;
  margin-bottom: 32px;
  box-shadow: 0 4px 20px rgba(59, 130, 246, 0.3);
`;

const HeaderContent = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
  text-align: center;
`;

const CompanyName = styled.h1`
  font-size: 28px;
  font-weight: 700;
  margin: 0 0 8px 0;
  color: white;
`;

const CompanyTagline = styled.p`
  font-size: 16px;
  margin: 0;
  opacity: 0.9;
  color: white;
`;

const NavBar = styled.nav`
  max-width: 1200px;
  margin: 0 auto 32px auto;
  padding: 0 24px;
  display: flex;
  justify-content: center;
  gap: 8px;
  background: white;
  border-radius: 16px;
  padding: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
`;

const NavButton = styled.button`
  background: ${({ active }) => (active ? 'linear-gradient(135deg, #3B82F6, #1D4ED8)' : 'transparent')};
  color: ${({ active }) => (active ? '#FFFFFF' : '#64748B')};
  border: none;
  border-radius: 12px;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 250ms ease-in-out;
  outline: none;
  
  &:hover {
    background: ${({ active }) => (active ? 'linear-gradient(135deg, #3B82F6, #1D4ED8)' : '#F1F5F9')};
    color: ${({ active }) => (active ? '#FFFFFF' : '#3B82F6')};
    transform: translateY(-1px);
  }
`;

const MainContent = styled.main`
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
`;

const OverviewContainer = styled.div`
  max-width: 900px;
  margin: 0 auto;
`;

const Section = styled.section`
  margin-bottom: 32px;
`;

const List = styled.ul`
  margin: 0 0 16px 0;
  padding-left: 24px;
  color: #1E293B;
  line-height: 1.6;
`;

const ListItem = styled.li`
  margin-bottom: 8px;
`;

const Highlight = styled.span`
  color: #10B981;
  font-weight: 600;
`;

const Table = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 24px;
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
`;

const Th = styled.th`
  background: linear-gradient(135deg, #3B82F6, #1D4ED8);
  color: white;
  padding: 16px;
  font-size: 16px;
  font-weight: 600;
  text-align: center;
`;

const Td = styled.td`
  border: 1px solid #E5E7EB;
  padding: 16px;
  font-size: 16px;
  text-align: center;
  color: #1E293B;
  
  &:nth-child(even) {
    background-color: #F8FAFC;
  }
`;

const Timeline = styled.div`
  display: flex;
  gap: 16px;
  margin-top: 24px;
  flex-wrap: wrap;
  justify-content: center;
`;

const Milestone = styled.div`
  background: linear-gradient(135deg, #3B82F6, #1D4ED8);
  color: white;
  border-radius: 12px;
  padding: 16px 24px;
  font-weight: 600;
  font-size: 16px;
  min-width: 140px;
  text-align: center;
  box-shadow: 0 4px 20px rgba(59, 130, 246, 0.3);
  transition: all 250ms ease-in-out;
  
  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 30px rgba(59, 130, 246, 0.4);
  }
`;

const StatsGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 24px;
  margin: 32px 0;
`;

const StatCard = styled(Card)`
  text-align: center;
  
  h3 {
    color: #3B82F6;
    font-size: 32px;
    font-weight: 700;
    margin: 0 0 8px 0;
  }
  
  p {
    color: #64748B;
    margin: 0;
  }
`;

function Overview() {
  return (
    <OverviewContainer>
      <H1 align="center" color="primary" style={{ marginBottom: '32px' }}>
            
      </H1>
      
      <StatsGrid>
        <StatCard>
          <H3>20-30%</H3>
          <Text color="secondary">  </Text>
        </StatCard>
        <StatCard>
          <H3>12-18</H3>
          <Text color="secondary">  </Text>
        </StatCard>
        <StatCard>
          <H3>90%+</H3>
          <Text color="secondary">  </Text>
        </StatCard>
        <StatCard>
          <H3>20%</H3>
          <Text color="secondary">  </Text>
        </StatCard>
      </StatsGrid>

      <Section>
        <H2 color="primary">  </H2>
        <List>
          <ListItem><strong>   ?</strong></ListItem>
          <ListItem> <strong>:</strong> //FMS   ,   ,   </ListItem>
          <ListItem> <strong>  :</strong> <Highlight>  </Highlight>, <Highlight> </Highlight>, <Highlight> </Highlight>, <Highlight>B2C </Highlight></ListItem>
        </List>
      </Section>

      <Section>
        <H2 color="primary">  ( )</H2>
        <List>
          <ListItem>       /   </ListItem>
          <ListItem>/     AWS/Azure  /</ListItem>
        </List>
        <List>
          <ListItem>:   ( )</ListItem>
          <ListItem>:   ( )</ListItem>
          <ListItem>:   ()</ListItem>
          <ListItem>:   (/)</ListItem>
          <ListItem>:    ( )</ListItem>
        </List>
      </Section>

      <Section>
        <H2 color="primary"> </H2>
        <Table>
          <thead>
            <tr>
              <Th></Th>
              <Th></Th>
              <Th>  </Th>
              <Th> </Th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <Td>B2B </Td>
              <Td> /</Td>
              <Td> +</Td>
              <Td><Highlight> 20~30%</Highlight></Td>
            </tr>
            <tr>
              <Td>B2C </Td>
              <Td></Td>
              <Td>   </Td>
              <Td><Highlight>  </Highlight></Td>
            </tr>
            <tr>
              <Td> </Td>
              <Td> </Td>
              <Td> </Td>
              <Td><Highlight> 20%</Highlight></Td>
            </tr>
            <tr>
              <Td> </Td>
              <Td> </Td>
              <Td>24/7  </Td>
              <Td><Highlight> 90%+</Highlight></Td>
            </tr>
          </tbody>
        </Table>
      </Section>

      <Section>
        <H2 color="primary">    </H2>
        <List>
          <ListItem> : <Highlight>$10,000~20,000</Highlight> /  : <Highlight>$1,000~2,500</Highlight></ListItem>
          <ListItem> (2025): <Highlight>   8,000</Highlight> (  20~50%)</ListItem>
          <ListItem> : <Highlight>12~18</Highlight> /   20~30% </ListItem>
        </List>
      </Section>

      <Section>
        <H2 color="primary"> </H2>
        <List>
          <ListItem>Signify( Philips Lighting): AWS ,   18%,  12%</ListItem>
          <ListItem>  :  , 6 ,  15%</ListItem>
        </List>
      </Section>

      <Section>
        <H2 color="primary">  </H2>
        <Timeline>
          <Milestone>3:  </Milestone>
          <Milestone>6: B2C  </Milestone>
          <Milestone>12:  </Milestone>
        </Timeline>
      </Section>
    </OverviewContainer>
  );
}

const pages = [
  { key: 'overview', label: '', component: <Overview /> },
  { key: 'arch', label: '', component: <CloudArchitecture /> },
  { key: 'dashboard', label: ' ', component: <ExecutiveDashboard /> },
  { key: 'journey', label: ' ', component: <CustomerJourney /> },
  { key: 'compare', label: ' ', component: <TechArchComparison /> },
];

function App() {
  const [page, setPage] = useState('overview');
  const currentPage = pages.find(p => p.key === page);
  
  return (
    <AppContainer>
      <Header>
        <HeaderContent>
          <CompanyName>JUNGHO GROUP</CompanyName>
          <CompanyTagline> </CompanyTagline>
        </HeaderContent>
      </Header>
      
      <NavBar>
        {pages.map(p => (
          <NavButton
            key={p.key}
            active={page === p.key}
            onClick={() => setPage(p.key)}
          >
            {p.label}
          </NavButton>
        ))}
      </NavBar>
      
      <MainContent>
        {currentPage && currentPage.component}
      </MainContent>
    </AppContainer>
  );
}

export default App; 